export class Item {
  name;
  images;

  constructor(name, images) {
    this.name = name;
    this.images = images;
  }

  getName() {
    return this.name;
  }

  getClearName() {
    return this.name.replace(/\./g, '_');
  }

  getImages() {
    return this.images;
  }
}



